<template>
  <da-panel title="系统功能导航" width="100%" height="auto" :border="false" min-height="200px" padding="0" ref="panelCount">
    <div class="link_wrap">
      <div class="link_whole" :class="'link_whole_'+pIndex" v-for="(pItem,pIndex) in dataList" :key="pIndex">
        <a v-for="(item,index) in pItem.children" :key="index" :href="item.url" class="tab_node" :class="'tab_'+ (index +pItem.column)" @click.prevent="toModule(item)" :style="{'background-color':colors[(index +pItem.column)%15]}">
          <div class="node_wrapper">
            <img :title="item.title" :src=" item.image">
            <div class="tab_title">{{item.title}}
            </div>
          </div>
        </a>
      </div>
    </div>
  </da-panel>
</template>

<script>
import u235 from "@/assets/images/home/u235.png";
import u237 from "@/assets/images/home/u237.png";
import u239 from "@/assets/images/home/u239.png";
import u241 from "@/assets/images/home/u241.png";
import u243 from "@/assets/images/home/u243.png";
import u245 from "@/assets/images/home/u245.png";
import u247 from "@/assets/images/home/u247.png";
import u249 from "@/assets/images/home/u249.png";
import u251 from "@/assets/images/home/u251.png";
import u253 from "@/assets/images/home/u253.png";
import u255 from "@/assets/images/home/u255.png";
import u257 from "@/assets/images/home/u257.png";
import u259 from "@/assets/images/home/u259.png";
export default {
  data() {
    return {
      props1: {
        label: "name",
        children: "zones",
        isLeaf: "leaf"
      },
      colors: [
        "#0C7E0E",
        "#426896",
        "#AF399C",
        "#DC4D5B",
        "#4617B4",
        "#3B65BF",
        "#177D86",
        "#2D004E",
        "#00D8CC",
        "#004060",
        "#4E0038",
        "#33CC00",
        "#E36B19"
      ],

      dataList: [
        {
          column: 0,
          children: [
            {
              title: "企业能耗总览",
              id: "company",
              image: u249,
              url: "energyUseOverview"
            },
            {
              title: "企业能效地图",
              id: "company",
              image: u237,
              url: "efficiencyMapAnalysis"
            },
            {
              title: "综合能耗分析",
              id: "company",
              image: u247,
              url: "energyConsumptionAnalysis"
            },
            {
              title: "综合能效对标分析",
              id: "company",
              image: u251,
              url: "comprehensiveTargetAnalysis"
            }
          ]
        },
        {
          column: 4,
          children: [
            {
              title: "节能分析",
              id: "company",
              image: u253,
              url: "energySavingAnalysis"
            },
            {
              title: "用能成本分析",
              id: "company",
              image: u235,
              url: "costAnalysis"
            },
            {
              title: "峰谷平用电分析",
              id: "company",
              image: u245,
              url: "energyPeakAnalysis"
            }
          ]
        },
        {
          column: 7,
          children: [
            {
              title: "告警中心",
              id: "company",
              image: u255,
              url: "alarmCentre"
            },
            {
              title: "用电监测",
              id: "company",
              image: u257,
              url: "electricityMonitoring"
            },
            {
              title: "电能质量监测",
              id: "company",
              image: u241,
              url: "powerQualityAnalysis"
            }
          ]
        },
        {
          column: 10,
          children: [
            {
              title: "实时数据监测",
              id: "company",
              image: u239,
              url: "tabularDataQuery"
            },
            {
              title: "计量网络监测",
              id: "company",
              image: u243,
              url: "measurementNetworkMonitoring"
            },
            {
              title: "设备运行状态监测",
              id: "company",
              image: u259,
              url: "equipmentMonitoring"
            }
          ]
        }
      ]
    };
  },
  methods: {
    toModule(item) {
      console.log(item);
      this.$router.push({
        path: item.url
      });
    }
  }
};
</script>


<style scoped>
.link_wrap {
  width: 1041px;
  margin: 0 auto;
}

.link_whole {
  white-space: normal;
  display: inline-block;
  vertical-align: top;
}
.link_whole_0 {
  width: 387px;
}
.link_whole_1,
.link_whole_3 {
  width: 198px;
}
.link_whole_2 {
  width: 258px;
}

.tab_node {
  text-align: center;
  margin: 5px;
  float: left;
  position: relative;
  box-sizing: border-box;
}

.tab_node img {
  color: #fff;
  width: 56px;
  vertical-align: top;
}

.tab_node {
  text-decoration: none;
  color: #fff;
  display: table;
}

.tab_node:hover {
  box-shadow: 0 0 6px 3px #b7b2b2;
}

.tab_node .tab_title {
  font-size: 16px;
  line-height: 18px;
  padding: 0;
  margin-top: 9px;
}

.tab_3 {
  width: 377px;
  height: 120px;
}

.tab_0,
.tab_7 {
  width: 248px;
  height: 248px;
}
.tab_4,
.tab_5,
.tab_6,
.tab_10,
.tab_11,
.tab_12 {
  width: 188px;
  height: 119px;
}
.tab_1,
.tab_2,
.tab_8,
.tab_9 {
  width: 119px;
  height: 119px;
}

.node_wrapper {
  display: table-cell;
  vertical-align: middle;
}

/* @media (min-width: 1600px) {
  .link_wrap {
    width: 1600px;
  }
}

@media (min-width: 1200px) and (max-width: 1600px) {
  .link_wrap {
    width: 1000px;
  }
}

@media (max-width: 1200px) {
  .link_wrap {
    width: 100%;
  }
  .tab_node {
    width: 100%;
    height: 8em;
  }
} */
</style>



