<template>
  <div class="clearfix">
    <!-- metro -->
    <color-block></color-block>
    <da-panel :no-head="true" margin="10px 0 50px">
      <div class="wel-msg">
        <div class="wel-msg-base">
          <P>当前用户：{{user}}</P>
          <P>所属部门：{{userDepartment}}</P>
          <P>授予角色：{{userRole}}</P>
        </div>
        <img src="../../assets/images/logo.png">
        <div class="wel-msg-company">
          <p>开发者支持：韶关云舜综合能源科技有限公司</p>
          <p>Copyright © 2018-2023 云舜科技&nbsp;. All rights reserved.&nbsp;&nbsp;版本：Ver 1.0</p>
        </div>
      </div>
    </da-panel>
    <div class="wel-footer">
      <span @click="redirectMeterAccount" class="link-a">在线表计：{{onlineCount}}个</span>
      <span @click="redirectMeterAccount" class="link-a">离线表计：{{offlineCount}}个</span>
      <span @click="redirectAlarmCentre" class="link-a">告警：{{alarmCount}}条</span>
      <span @click="redirectAlarmCentre" class="link-a">{{alarmMessage}}</span>
    </div>
  </div>
</template>

<script>
import colorBlock from "./template/colorBlock";
export default {
  components: {
    colorBlock
  },
  data() {
    return {
      //   当前用户
      user: "",
      //   用户部门
      userDepartment: "",
      //   用户角色
      userRole: "",
      //   在线表计
      onlineCount: "",
      //   离线表计
      offlineCount: "",
      //   报警个数
      alarmCount: "",
      //   报警信息
      alarmMessage: "",
      //   报警信息队列
      alarmMessageArr: []
      //   count: 0
    };
  },
  methods: {
    loadData() {
      this.getCurrentUserAccount();
      this.getCurrentUserRoles();
    },
    /**
     * 获取当前用户
     */
    getCurrentUserAccount() {
      this.$ajax.get("rest/user/currentUser").then(res => {
        if (res.data.status) {
          let userDetail = res.data.records;
          this.user = userDetail.name;
          this.userDepartment = userDetail.orgName;
        }
      });
    },
    /**
     * 获取当前用户的角色
     */
    getCurrentUserRoles() {
      this.userRole = "";
      this.$ajax.get("rest/user/currentUserRole").then(res => {
        if (res.data.status) {
          let data = [];
          data = res.data.records;
          data.forEach(role => {
            if (this.userRole === "") {
              this.userRole += role.roleName;
            } else {
              this.userRole += "," + role.roleName;
            }
          });
        }
      });
    },
    /**
     * 查询今日告警总数
     */
    getTodayAlarm() {
      let now = new Date();
      let startTime = this.$moment(now, "YYYY-MM-DD");
      let endTime = startTime;
      this.alarmCount = 0;
      this.$ajax
        .get("rest/analysisData/alarmData", {
          params: {
            title: "首页告警", // String,功能标题
            userIds: null, // String,用户ID
            alarmLevels: null, // String,告警级别
            alarmGroups: null, // String,告警分组ID
            alarmCodes: null, // String,告警项编码
            receiveTypes: null, // String,用户接收告警类型
            orgIds: null, // String,部门ID
            equipIds: null, // String,设备ID
            productIds: null, // String,产品ID
            alarmItems: null, // String,告警项ID
            startTime: startTime, // String,开始时间
            endTime: endTime, // String,结束时间
            isCount: false, // Boolean,是否统计
            countCycle: "3", // String,统计周期（1为小时，3为日，5为月，8为年）
            groupBys: "000000100" // String,按什么统计或是否显示，111111111（[部门][级别][分类][编码][确认人][是否确认][时间][设备][产品]）
          }
        })
        .then(res => {
          if (res.data.status) {
            let data = [];
            data = res.data.records;
            if (data && data.length > 0) {
              this.alarmCount = data.length;
              data.forEach(item => {
                this.alarmMessageArr.push(item.alarmDetail);
              });
            }
          }
        });
    },
    /**
     * 查询在线离线表计信息
     */
    getOnlineMeterCountInfor() {
      this.onlineCount = 0;
      this.offlineCount = 0;
      this.$ajax
        .get("rest/analysisData/onlineMeter", {
          params: {
            title: "首页告警", // String,功能标题
            orgIds: null, // String,部门ID
            energySubItems: null, // String,用能分项ID
            energyIds: null, // String,能源ID
            isOnline: null, // 布尔型,是否查询在线数据
            isCollection: false, // 布尔型,是否查询采集过数据的表计/采集项
            isCount: true, // 布尔型,是否统计
            groupBys: "00010" // String,按什么统计或是否显示，11111（[部门][用能分项][能源][表计][采集项]）,第四个和第五个字符必有一个为1
          }
        })
        .then(res => {
          if (res.data.status) {
            let data = [];
            data = res.data.records;
            if (data && data.length > 0) {
              data.forEach(item => {
                if (item.online === 1) {
                  this.onlineCount = item.num;
                } else {
                  this.offlineCount = item.num;
                }
              });
            }
          }
        });
    },
    redirectMeterAccount() {
      this.$router.push({ path: "/tabulationEquipmentLedger" });
    },
    redirectAlarmCentre() {
      this.$router.push({ path: "/alarmCentre" });
    }
  },
  mounted() {
    this.loadData();
    this.getTodayAlarm();
    this.getOnlineMeterCountInfor();

    /*     setInterval(() => {
      this.alarmMessage = this.alarmMessageArr[this.count];
      this.count++;
      if (this.count == 4) {
        this.count = 0;
      }
    }, 2000); */
  }
};
</script>
<style scoped>
.wel-msg {
  display: flex;
  width: 100%;
  padding: 10px;
  align-items: flex-end;
}

.wel-msg-base {
  font-size: 16px;
  line-height: 33.3px;
}
.wel-msg-company {
  line-height: 2em;
}
.wel-msg > div {
  flex: 1;
}
.wel-msg > img {
  width: auto;
  height: 100px;
}
.wel-footer {
  position: fixed;
  bottom: 0;
  background: #fff;
  width: 100%;
  margin-left: -30px;
  padding: 10px;
  border-top: 1px solid #ddd;
  background-color: #eee;
  /* z-index: -1; */
}
.wel-footer > span {
  padding: 0 10px;
}
</style>



